<template>
  <div class="container-videotool">
    <div class="test_two_box" ref="center">
      <video id="myVideo1" class="video-js">
        <source
          :src="src"
          type="video/mp4"
        />
      </video>
    </div>
    <div class="showFace" @click="handleplay" :style="{display:myDis}">
      <Iconplay></Iconplay>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import Iconplay from '@/components/Iconplay'
export default {
  name: "TestTwo",
  data() {
    return {
      windowWidth: 0,
      windowHeight: 0,
      myPlayer1:null,
      myDis:"block"
    };
  },
  props:{
    src:{
      type:String,
      required:true
    }
  },
  components:{
      Iconplay
  },
  mounted() {
    this.setSize;
    this.initVideo(this.windowWidth, this.windowHeight);
    
  },
  methods: {
    initVideo(a, b) {
      //初始化视频方法
       this.myPlayer1 = this.$video(myVideo1, {
        //确定播放器是否具有用户可以与之交互的控件。没有控件，启动视频播放的唯一方法是使用autoplay属性或通过Player API。
        controls: true,
        //自动播放属性,muted:静音播放
        muted: false,//是否静音
        // autoplay: "muted",
        //建议浏览器是否应在<video>加载元素后立即开始下载视频数据。
        preload: "auto",
        //设置视频播放器的显示宽度（以像素为单位）
        width: `${a}px`,
        //设置视频播放器的显示高度（以像素为单位）
        height: `${b}px`,
      });
    },
    handleplay(){
      this.myPlayer1.play();
      this.myDis = "none";
    // console.log(this.myPlayer.play)
    }
  },
  computed: {
      setSize(){
          this.windowWidth = this.$refs.center.clientWidth;
          this.windowHeight =this.$refs.center.clientHeight;
      }
  },
}
</script>

<style scoped lang="less">
.container-videotool{
  width: 100%;
  height: 100%;
  position: relative;
  .test_two_box {
    width: 100%;
    height: 100%;
    clip-path: polygon(0 0, 100% 0, 100% calc(100% - 30px), calc(100% - 30px) 100%, 0 100%);
    .video-js {
      .vjs-tech {
        object-fit: cover;
        opacity: 0.8;
      }
      
    }
    }
  .showFace{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    background-image: url('~@/assets/home/home3/6.jpg');
    background-repeat: no-repeat;
    background-size: 100% 100%;
    clip-path: polygon(0 0, 100% 0, 100% calc(100% - 30px), calc(100% - 30px) 100%, 0 100%);
    &::after{
      content: "";
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      left: 0;
      top: 0;
      background: black;
      opacity: 0.5;
      z-index: 2;
    }
  }
}

</style>