<script>
import Title from "@/components/Title";
import Follow from "@/components/Follow";
import forImage from "@/mixins/forImage.js"
export default {
    mixins:[forImage([ 'https://lihaibo-li.github.io/lolp/dist/'+require('@/assets/home/home6/1.png')])],
  components: {
    Title,
    Follow,
  },
  data() {
    return {
      lunboId: ['0%','-25%',"-50%","-75%"],
      index:0,
      isChang:true,
      timer:null,
      timer1:null,
      NewClient:0
    };
  },
  mounted() {
    this.NewClient = document.documentElement.clientWidth;
    if(this.NewClient<=1024){
       this.changeP();
    }else{
      return;
    }
  
  },
  destroyed() {
    clearInterval(this.timer);
  },
  methods: {
   changeP(){
      clearInterval(this.timer);
     this.timer = setInterval(() => {
        if(this.index<this.lunboId.length-1){
          this.isChang = true
          this.index++;
        }else{
          this.isChang = false;
          this.index = 0;
        }
    }, 2500);
   },
   MoveLeft(){
     clearInterval(this.timer);
     if(this.index <=0 ){
       this.isChang = false;
       this.index = this.lunboId.length - 1;
     }else{
       this.isChang = true;
       this.index = this.index - 1;
     }
     clearTimeout(this.timer1);
     this.timer1 = setTimeout(()=>{
       this.changeP();
     },3000)
   },
   MoveRight(){
     clearInterval(this.timer);
     if(this.index>=this.lunboId.length - 1){
       this.isChang = false;
       this.index = 0;
     }else{
       this.isChang = true;
       this.index = this.index +1;
     }
     clearTimeout(this.timer1);
     this.timer1 = setTimeout(()=>{
       this.changeP();
     },3000)
   }
  },
};
</script>

