        "id":1,
        "url":`${loadUrl1}`,
        "title":"激烈对抗",
        "text":"与好友组成队伍，在5v5的MOBA战斗中展现自己的实力。你所梦想的高超竞技都在这里，专为手机设计，英雄操控精心改良，对战节奏更加明快。"
      },
      {
        "id":2,
        "url":`${loadUrl2}`,
        "title":"经典英雄",
        "text":"用拉克丝的终极闪光驱逐黑暗，或用亚索的狂风绝息斩追风杀敌。随时随地，用你最爱的英雄来一场精彩对决。"
      },
      {
        "id":3,
