  this.widthBottom = `100%`;
  this.line2 = `3rem`;

},
methods: {
  handleChange(e){
    this.isActive = e;
  },
  handleLine(){
  this.heightLeft = `100%`;
  this.widthBottom = `100%`;
  this.line2 = `3rem`
  },
  handleAdd(){
    this.MobileId = this.MobileId +1 ;
    if(this.MobileId >2){
